e;\n\t\twidth: 12px;\n\t\theight: 12px;\n\t\tbackground: #ccc;\n\t\tborder-radius: 50%;\n\t\ttransition: 0.2s; /*  */\n\t}\n\n\t&:active::-webkit-slider-thumb {\n\t\twidth: 16px;\n\t\theight: 16px;\n\t}\n}\n\n:global(.hidden) {\n\tdisplay: none;\n}\n\n:global(.icon) {\n\twidth: 1em;\n\theight: 1em;\n\tvertical-align: -0.15em;\n\tfill: currentColor;\n\toverflow: hidden;\n}\n\n:global(.touch-yellow) {\n\t@include touch-color;\n}\n:global(.touch-blue) {\n\t@include touch-color($blue-primary, $blue-second);\n}\n:global(.touch-green) {\n\t@include touch-color($green-primary, $green-second);\n}\n:global(.touch-cyan) {\n\t@include touch-color($cyan-primary, $cyan-second);\n}\n:global(.touch-purple) {\n\t@include touch-color($purple-primary, $purple-second);\n}\n"],"sourceRoot":""}]);
