import React from 'react';
import { ChordCard, useBoardContext } from '../guitar-board';
import cx from 'classnames';
import styles from './chord-list.module.scss';
import { Icon } from '..';
export const ChordList = ({ data, title, intro, disableCollect }) => {
    const { collection, dispatchCollection } = useBoardContext();
    const handleRemoveChord = () => {
        const index = collection.findIndex((x) => x.title === title);
        collection[index].data.splice(index, 1);
        dispatchCollection({ type: 'set', payload: collection });
    };
    const handleRemoveCollection = () => {
        const index = collection.findIndex((x) => x.title === title);
        collection.splice(index, 1);
        dispatchCollection({ type: 'set', payload: collection });
    };
    const dataView = data.map((item, index) => {
        return (React.createElement(ChordCard, { key: index, disableCollect: disableCollect, onRemoveCollection: handleRemoveChord, className: styles.card, size: 100, taps: item.taps, title: item.title }));
    });
    return (React.createElement("div", { className: styles.list },
        React.createElement("div", { className: cx('buitar-primary-button', styles['title-view'], 'touch-yellow') },
            title,
            React.createElement("div", null, intro),
            !disableCollect && (React.createElement(Icon, { name: "icon-close", className: styles['list-remove'], onClick: handleRemoveCollection }))),
        React.createElement("div", { className: cx('scroll-without-bar') },
            React.createElement("div", { className: styles['data-list'] }, dataView))));
};
